.search_bar__item {
    z-index: 2;
}

.search_bar__item:hover {
    cursor: pointer;
    background-color: whitesmoke;
    transition: all .03s linear;
}

.search_bar__data {
    position: absolute;
    display: inline-flex;
    margin: 5px;
}

.search_bar__data .title{
    display: contents;
    font-size: 14px;
    font-weight: 500;
}

.search_bar__data .year{
    display: contents;
    font-size: 12px;
    font-weight: 300;
}