.movie {
    width: 130px;
    height: 260px;
    margin: 0 10px 20px 10px;
    border-radius: 10px;
    box-shadow: 0 50px 100px -20px rgb(50, 50, 93, 0.25),
     0 30px 60px -30px rgb(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .figure {
    display: flex;
    justify-content: center;
    align-items: center;
}

.movie img {
    position: relative;
    width: 100%;
    top: 10px;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .figcaption {
    position: absolute;
    text-align: center;
    font-variant: small-caps;
    overflow: hidden;
    opacity: 0;
    font-size: 16px;
    font-weight: 600;
    max-height: 165px;
}

.movie .figure:hover > img {
    cursor: pointer;
    transition: all .1s linear;
    top: 0;
    opacity: 0.2;
}

.movie .figure:hover > .figcaption {
    cursor: pointer;
    transition: all .2s linear;
    opacity: .8;
}

.movie .figure:active > img {
    transition: all .03s linear;
    transform: scale(1.03);
}

.movie .title {
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-align: left;
    color: #2c2c2c;
    top: -5px;
    margin: 0 5px 0 5px;
    font-size: 16px;
    font-weight: 500;
}

.movie .title:not(:hover) {
    text-overflow: ellipsis;
}

.title span:hover {
    display: inline-block;
    animation-name: scroll-text;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-duration: 2s;
    animation-delay: 0s;
}

/* define the animation */
@keyframes scroll-text {
    50% {
        transform: translateX(calc(-100% + 115px));
    }
    100% {
        transform: translateX(calc(-100% + 115px));
    }
  }

.movie .year {
    position: relative;
    color: #adaeb9;
    top: -5px;
    margin: 0 5px 5px 5px;
    font-size: 16px;
    font-weight: 300;
}