.app {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.title {
    text-align: center;
    padding-top: 20px;
    font-weight: 300;
}

.ctrl {
    display: block;
    text-align: center;
}

.ctrl__select {
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.ctrl__search {
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.container {
    display: flex;
    justify-content: center;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80vh;
    font-weight: 300;
}

.movies {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    height: 100%;
    margin: 20px 5% 50px 5%;
}

.side .side__left {
    position: fixed;
    text-align: center;
    cursor: pointer;
    top: 50%;
    left: 5px;
    padding: 15px;
    font-size: 16px;
    font-weight: 400;
    opacity: 0.9;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.side .side__right {
    position: fixed;
    text-align: center;
    cursor: pointer;
    top: 50%;
    right: 5px;
    padding: 15px;
    font-size: 16px;
    font-weight: 400;
    opacity: 0.9;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}