.detail {
    position: fixed;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    top: 5%;
    width: 80vw;
    height: 90%;
    z-index: 100;
    border-radius: 20px;
    background-color: rgba(242,242,242,0.9);
}

.detail::-webkit-scrollbar {
    display: none;
}

.detail__close {
    float: right;
    margin: 15px 30px 0 0;
    color: rgba(0,0,0,0.8);
}

.detail__close:hover {
    cursor: pointer;
}

.detail__content {
    display: block;
    text-align: center;
    padding: 20px;
    font-size: 18px;
    font-weight: 300;
}

.detail__content h6 {
    font-style: italic;
    font-size: 16px;
    font-weight: 300;
}

.detail__content img {
    max-width: 100%;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail__video {
    width: 100%;
    height: 40vw;
    border-radius: 10px;
    border: 0;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}