.search_bar {
    background-color: white;
}

.search_bar .input {
    display: inline-block;
    font-size: 16px;
    font-weight: 400;
    margin: 5px;
    width: 245px;
}

.search_bar .items {
    position: absolute;
    display: block;
    text-align: left;
    overflow: hidden;
    width: 250px;
    padding: 5px;
    border-radius: 10px;
    background-color: white;
    z-index: 1;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.search_bar .items .image{
    width: 50px;
    border-radius: 5px;
}